import { Component, OnInit } from '@angular/core';
import OlMap from 'ol/Map';
import OlOverlay from 'ol/Overlay';
import OlXYZ from 'ol/source/XYZ';
import OlTileLayer from 'ol/layer/Tile';
import OlView from 'ol/View';

import {Overlay,  Feature} from 'ol/index';

import {Draw, Modify, Snap} from 'ol/interaction';
import VectorSource from 'ol/source/Vector';
import VectorLayer from 'ol/layer/Vector';

import { transform, fromLonLat } from 'ol/proj';

@Component({
  selector: 'app-general-map',
  templateUrl: './general-map.component.html',
  styleUrls: ['./general-map.component.css']
})

export class GeneralMapComponent implements OnInit {
  
  show : boolean =true;
  map: any;
  source: OlXYZ;
  vectorsource :any;
  layer: OlTileLayer;
  view: OlView;
  feature : Feature;
  
  Layers : any=[]; 

  draw: Draw;

  constructor() { }

  ngOnInit(): void {
    this.vectorsource = new VectorSource();
    this.show=true;
      this.source = new OlXYZ({
        title : 'Base layer',
        url: 'https://services.arcgisonline.com/arcgis/rest/services/' +
        'ESRI_Imagery_World_2D/MapServer/tile/{z}/{y}/{x}',
          projection: 'EPSG:4326',
          tileSize: 512, // the tile size supported by the ArcGIS tile service
          maxResolution: 180 / 512, // Esri's tile grid fits 180 degrees on one 512 px tile
          wrapX: true,
      });

      this.layer = new OlTileLayer({
        source: this.source
      });
        
      this.Layers.push(this.layer);

      this.view = new OlView({
        center: fromLonLat([6.661594, 50.433237]),
        zoom: 0
      });

      this.map = new OlMap({
        target: 'map',
        layers: [this.layer],
        view: this.view
      });

      this.addVectorLayer();
  }


  addVectorLayer(){
    let vector = new VectorLayer({
        source : this.vectorsource
    });
    this.map.addLayer(vector);
  }

  drawlayers(drawtype :  any){
    this.map.removeInteraction(this.draw);

     this.draw = new Draw({
      source :  this.vectorsource,
      type : drawtype
    });

    this.map.addInteraction(this.draw);
    // var snap = new Snap({source: this.vectorsource});
    // this.map.addInteraction(snap);
    
    this.draw.on('drawend', ($event) => {
      alert("Place Added Sccessfully!!!"+ $event.feature.values_.geometry.flatCoordinates);
     //this.map.removeInteraction(draw);
    });
    this.show=false;
  }

  stopEditing(){
    this.map.removeInteraction(this.draw);
    this.show=true;
  }
  





openPan : any;

/* Set the width of the sidebar to 250px (show it) */
openNav() {
  this.openPan = "250px";
}

/* Set the width of the sidebar to 0 (hide it) */
closeNav() {
  document.getElementById("mySidepanel").style.width = "0";
}








  getInfo($event){
   
    let popup = document.getElementById("popupbody");  
    this.map.on('singleclick', function(evt) {
      let latlon=transform(evt.coordinate, 'EPSG:3857', 'EPSG:4326');
      var lon = latlon[0];
      var lat = latlon[1];

      popup.innerHTML = "<span> Coordinates are : Long "+lon+" , Lat "+lat+"</span>"


      var container = document.getElementById('popup');
      var content = document.getElementById('popup-content');
      var closer = document.getElementById('popup-closer');
     
      var overlay = new OlOverlay({
          element: container,
          autoPan: true,
          autoPanAnimation: {
              duration: 250
          }
      });
       

      closer.onclick = function() {
        overlay.setPosition(undefined);
        closer.blur();
        return false;
      };
      debugger
      if (true) {
         content.innerHTML = '<b>Hello world!</b><br />I am a popup.'+latlon;
          overlay.setPosition(latlon);
       } else {
          overlay.setPosition(undefined);
          closer.blur();
      }
      this.map.addOverlay(overlay);
    });

  }
  
    
}



