import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { GeneralMapComponent } from './general-map/general-map.component';
import { HomePageComponent } from './home-page/home-page.component';
import { StreetMapComponent } from './street-map/street-map.component';
import { ExtMapComponent } from './ext-map/ext-map.component';


const routes: Routes = [
    { path: '', component: HomePageComponent },
    { path: 'generalmap',  component: GeneralMapComponent },
    { path: 'streetmap',  component: StreetMapComponent },
    {path:'extmap', component:ExtMapComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
