import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';
import { BaseServiceService } from '../base/base-service.service';

@Injectable({
  providedIn: 'root'
})
export class StreetMapService extends BaseServiceService{

  constructor(private http : HttpClient ) {
    super() 
   }

  private REST_API_SERVER = "http://localhost:8080/demoproject/services/geo/pointdata";

  public sendGetRequest(){
    return this.http.post(this.serverurl + 'geo/pointdata','',this.httpOption)
    .pipe(catchError(this.handleError));
  }
}
