import { Component, OnInit } from '@angular/core';
import Map from 'ol/Map';
import Overlay from 'ol/Overlay';
import View from 'ol/View';
import {toStringHDMS} from 'ol/coordinate';
import TileLayer from 'ol/layer/Tile';
import {fromLonLat, toLonLat} from 'ol/proj';
import OSM from 'ol/source/OSM';
import MousePosition from 'ol/control/MousePosition';
import {Vector as VectorLayer} from 'ol/layer';
import {Vector as VectorSource} from 'ol/source';

import Draw from 'ol/interaction/Draw';
import Snap from 'ol/interaction/Snap';
import GeoJSON from 'ol/format/GeoJSON';


import Style from 'ol/style/Style';
import Stroke from 'ol/style/Stroke';
import Fill from 'ol/style/Fill';


declare var jQuery: any;
declare var $: any; // ADD THIS


@Component({
  selector: 'app-ext-map',
  templateUrl: './ext-map.component.html',
  styleUrls: ['./ext-map.component.css']
})
export class ExtMapComponent implements OnInit {

  constructor() { }


  soilproperties=['fid_1','vill_id','cropping_system','village', 'state','district','year'];

  ngOnInit(): void {

    

var layer = new TileLayer({
  source: new OSM()
});

var map = new Map({
  layers: [layer],
  target: 'map',
  view: new View({
    center: [0, 0],
    zoom: 2
  })
});

var pos = fromLonLat([16.3725, 48.208889]);



// Vienna label
var vienna = new Overlay({
  position: pos,
  element: document.getElementById('vienna')
});
map.addOverlay(vienna);

// Popup showing the position the user clicked
var popup = new Overlay({
  element: document.getElementById('popup')
});
map.addOverlay(popup);

// map.on('click', function(evt) {
//   var element = popup.getElement();
//   var coordinate = evt.coordinate;
//   var hdms = toStringHDMS(toLonLat(coordinate));

//   $(element).popover('destroy');
//   popup.setPosition(coordinate);
//   $(element).popover({
//     placement: 'right',
//     animation: false,
//     html: true,
//     content: '<code>' + hdms + '</code>'
//   });
  
//   $(element).popover('show');
// });

map.addControl(new MousePosition({
  projection: 'ESPG:4326',
  undefinedHTML: '&nbsp'
}));
  
 
// this is were the drawn features go
var drawVector = new VectorLayer({
  source: new VectorSource(),
  style: new Style({
    stroke: new Stroke({
      color: 'rgba(100, 255, 0, 1)',
      width: 2
    }),
    fill: new Fill({
      color: 'rgba(100, 255, 0, 0.3)'
    })
  })
});
map.addLayer(drawVector);
var baseVector = new VectorLayer({
  source: new VectorSource({
    format: new GeoJSON(),
    url: 'https://ahocevar.com/geoserver/wfs?service=wfs&request=getfeature&typename=topp:states&cql_filter=STATE_NAME=\'Idaho\'&outputformat=application/json'
  })
});

map.addLayer(baseVector);
  var typeSelect = document.getElementById('type');
  var drawInteraction, tracingFeature, startPoint, endPoint;
  var drawing = false;

  var snapInteraction = new Snap({
    source: baseVector.getSource()
  });

function addInteraction() {
  debugger
  var value = typeSelect.nodeValue;
  if (value !== 'None') {
    drawInteraction = new Draw({
      source: drawVector.getSource(),
      type: typeSelect.nodeValue
    });
    drawInteraction.on('drawstart', function () {
      drawing = true;
    });
    
    drawInteraction.on('drawend', function (evt) {
      drawing = false;
      tracingFeature = null;
      debugger
       $('#myModal').modal('show');
      
    });
    map.addInteraction(drawInteraction);
    map.addInteraction(snapInteraction);
  }
}

typeSelect.onchange = function() {
  debugger
  map.removeInteraction(drawInteraction);
  map.removeInteraction(snapInteraction);
  addInteraction();
};
addInteraction();

}
}

